<template>
<div style='text-align:center;' >
  <Flatpickr
    ref='flatpickr'
    v-model='selectedDate'
    :options="options" ></Flatpickr>
  <div style='clear:both;' ></div>
</div>
</template>
<script type="text/javascript">
import moment from 'moment'
import ScheduleMixin from '../../../mixins/ScheduleMixin'
import Mixins from '../../../mixins/Mixins'
export default {
  name: 'DatePicker',
  mixins: [ScheduleMixin, Mixins],
  props: {
    value: {
      type: String,
      default: () => {
        return moment().format('YYYY-MM-DD')
      }
    },
    appointments: {
      type: Array,
      default: () => { return [] }
    },
    hours: {
      type: Object,
      required: false
    },
    slotDuration: {
      type: Number,
      required: false
    }
  },
  data () {
    let vm = this
    return {
      selectedDate: this.date,
      options: {
        inline: true,
        altFormat: 'ddd, D MMM Y',
        minDate: moment().add(1, 'day').format('YYYY-MM-DD'),
        onMonthChange: (selectedDates, dateStr, instance) => {
          let currentMonth = instance.currentMonth + 1
          let currentYear = instance.currentYear
          let anchor = `${currentYear}-${currentMonth}-15`
          let newRange = {
            fromDate: moment(anchor).startOf('month').format('YYYY-MM-DD'),
            toDate: moment(anchor).endOf('month').format('YYYY-MM-DD')
          }
          this.$emit('datepicker:rangechanged', newRange)
        },
        disable: [
          (date) => {
            // return true to disable
            if (this.hours) {
              let hoursString = vm.getDaysOperatingHours(date, this.hours)
              if (!hoursString || hoursString === '') {
                return true
              }
              if (this.slotDuration) {
                let hasSlots = this.hasAvailableSlots(
                  moment(date).format('YYYY-MM-DD'),
                  hoursString,
                  this.slotDuration,
                  this.appointments)
                return !hasSlots
              }
            }
          }
        ]
      }
    }
  },
  watch: {
    selectedDate (date) {
      this.$emit('input', date)
    },
    appointments () {
      console.log('app changed')
      this.$refs.flatpickr.fp.redraw()
    },
    hours () {
      console.log('hrs changed')
      this.$refs.flatpickr.fp.redraw()
    }
  }
}
</script>
<style >
.flatpickr-calendar.inline {
    display: block;
    position: relative;
    margin-left: auto;
    margin-right: auto;
}
.flatpickr-input {
  display:none;
}
</style>



