<template>
<el-form
  ref="form"
  :model="formData"
  :rules='formRules'
  class='contact-details-form'
  label-position='top' >
  <el-form-item label="Your full name" prop="fullName" >
    <el-input v-model="formData.fullName"></el-input>
  </el-form-item>
  <el-form-item label="Your contact number" prop="phone" >
    <el-input
      type='tel'
      placeholder="Your contact number (start with 0)"
      v-model="formData.phone" >
      <el-autocomplete
        slot='prepend'
        class="inline-input"
        name='countryCode'
        v-model="formData.countryCode"
        :fetch-suggestions="searchCountries"
      ></el-autocomplete>
    </el-input>
  </el-form-item>
  <el-form-item label="Your e-mail address" prop="email" >
    <el-input v-model="formData.email" type='email' ></el-input>
  </el-form-item>
</el-form>
</template>
<script>
/* eslint-disable new-cap */
import { parse, format, asYouType } from 'libphonenumber-js'
import IntlPhoneNumberMixin from '../../../mixins/IntlPhoneNumberMixin'
import CountryCodeMixin from '../../../mixins/CountryCodeMixin'
export default {
  parse,
  format,
  asYouType,
  name: 'ContactDetailsForm',
  mixins: [IntlPhoneNumberMixin, CountryCodeMixin],
  props: {
    value: { type: Object, required: true }
  },
  mounted () {
    this.setCountryFromCountryCode()
  },
  computed: {
    phoneInputs () {
      return this.formData.phone
    },
    countryCode () {
      return this.formData.countryCode
    }
  },
  watch: {
    phoneInputs () {
      this.updateFormattedPhoneNumber()
    },
    countryCode () {
      this.setCountryFromCountryCode()
      this.updateFormattedPhoneNumber()
      this.$refs.form.validateField('phone')
    }
  },
  methods: {
    phoneNumberValidator (rule, value, callback) {
      // this.country.iso
      let result = this.validatePhoneNumber(this.country, value)
      if (result !== true) {
        callback(new Error(result))
      } else {
        callback()
      }
    },
    fullNameValidator (rule, value, callback) {
      if (value.trim().split(' ').length <= 1) {
        callback(new Error('Please provide your full name (first and last names)'))
      } else {
        callback()
      }
    },
    updateFormattedPhoneNumber () {
      if (this.country.iso) {
        let formattedNumber = this.getFormattedPhoneNumber(
                                this.country.iso,
                                this.formData.phone)
        this.formData.formattedNumber = formattedNumber
      } else {
        this.formData.formattedNumber = null
      }
    },
    validateModel () {
      let requiredKeys = ['fullName', 'email', 'phone', 'countryCode', 'formattedNumber', 'country']
      let actualKeys = Object.keys(this.value)
      for (let key of requiredKeys) {
        if (actualKeys.indexOf(key) === -1) {
          console.warn(`The object passed in with v-model needs to have a ${key} field. If you don't specifiy one on your model (even if its just null) your data won't be reliably reactive.`)
        }
      }
    }
  },
  data () {
    this.validateModel()
    return {
      formData: this.value,
      country: {},
      formRules: {
        fullName: [
          { required: true, message: 'Please provide your full name', trigger: 'change' },
          { validator: this.fullNameValidator, trigger: 'change' }
        ],
        email: [
          { type: 'email', required: true, message: 'Please provide a valid e-mail address', trigger: 'change' }
        ],
        phone: [
          { required: true, message: 'Please provide a valid phone number', trigger: 'change' },
          { validator: this.phoneNumberValidator, trigger: 'change' }
        ]
      }
    }
  }
}
</script>
<style >
.contact-details-form {
  padding:20px;
}
</style>



