<template>
<div>
  <template v-for="slot in slots" >
    <!-- <h5 v-if='slot.type === "break" ' >
      {{slot.startTime}} -
      {{slot.endTime}}
    </h5> -->
    <hr v-if='slot.type === "break"'/>
    <tile
      v-if='slot.type === "slot"'
      :class='"available-" + slot.available'
      @click.native='slotSelected(slot)' >
        <span slot='tile-side' class='el-icon-time' ></span>
        <span slot='tile-inner' >
        {{dateformat(slot.startTime, 'HH:mm')}} -
        {{dateformat(slot.endTime, 'HH:mm')}}
        </span>
    </tile>
  </template>
  <tile v-if='slots.length === 0' >
    <span slot='tile-inner' >No available slots</span>
  </tile>
</div>
</template>
<script>
import moment from 'moment'
import Mixins from '../../../mixins/Mixins'
import ScheduleMixin from '../../../mixins/ScheduleMixin'
import Tile from '../../ui/Tile'

export default {
  name: 'SlotPicker',
  mixins: [Mixins, ScheduleMixin],
  components: { Tile },
  props: {
    operationHours: { type: String, required: true },
    slotDuration: { type: Number, required: true },
    day: {
      type: String,
      default: () => { return moment().format('YYYY-MM-DD') }
    },
    appointments: {
      type: Array,
      default: () => { return [] }
    }
  },
  computed: {
    slots () {
      // todo: refactor into ScheduleMixin:
      let day = []
      let periods = this.getTimePeriods(this.operationHours)
      for (let period of periods) {
        let slots = this.getAvailableSlots(
          period.start,
          period.end,
          this.slotDuration,
          this.appointments,
          this.day)

        day.push({
          'type': 'break',
          'startTime': period.start,
          'endTime': period.end
        })
        day = day.concat(slots)
      }
      return day
    }
  },
  methods: {
    slotSelected (slot) {
      if (slot.available) {
        this.$emit('slotpicker:slotselected', slot)
      }
    }
  }
}
</script>
<style scoped>
.available-false {
  text-decoration: line-through;
  opacity: 0.6;
  background:#f1f1f1 !important;
}
</style>



